/* CUSTOM RADIO & CHECKBOXES
   http://stackoverflow.com/a/17541916/383904 */
.rad,
.ckb {
  cursor: pointer;
  user-select: none;
  -webkit-user-select: none;
  -webkit-touch-callout: none; }

.rad > input,
.ckb > input {
  /* HIDE ORG RADIO & CHECKBOX */
  visibility: hidden;
  position: absolute; }

/* RADIO & CHECKBOX STYLES */
.rad > i,
.ckb > i {
  /* DEFAULT <i> STYLE */
  display: inline-block;
  vertical-align: middle;
  width: 16px;
  height: 16px;
  border-radius: 50%;
  transition: 0.2s;
  box-shadow: inset 0 0 0 8px #fff;
  border: 1px solid #909191;
  background: #909191; }

/* CHECKBOX OVERWRITE STYLES */
.ckb > i {
  width: 25px;
  border-radius: 3px; }

.rad:hover > i {
  /* HOVER <i> STYLE */
  box-shadow: inset 0 0 0 3px #fff;
  background: #909191; }

.rad > input:checked + i {
  /* (RADIO CHECKED) <i> STYLE */
  box-shadow: inset 0 0 0 3px #fff;
  background: #15345e; }

/* CHECKBOX */
.ckb > input + i:after {
  content: "";
  display: block;
  height: 12px;
  width: 12px;
  margin: 2px;
  border-radius: inherit;
  transition: inherit;
  background: #909191; }

.ckb > input:checked + i:after {
  /* (RADIO CHECKED) <i> STYLE */
  margin-left: 11px;
  background: #15345e; }

