html {
  height: 100%;
  font-size: 16px;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

*,
*:after,
*:before {
  box-sizing: inherit; }

body {
  height: 100%;
  font-size: 16px;
  overflow-x: hidden;
  width: 100%;
  margin: 0 !important;
  padding: 0 !important; }

.main-container {
  min-height: 100%; }

.container {
  width: 100%;
  margin: 0;
  padding: 0; }

.modal {
  overflow-y: auto; }

.modal-open {
  overflow: auto;
  overflow-x: hidden; }

.modal-open[style] {
  padding-right: 0px !important; }

.row {
  margin: 0;
  padding: 0; }

.footnote,
.graph-footnote {
  font-family: "FFDINStdRegular";
  font-size: 0.75rem;
  max-width: 50rem;
  margin: 0 auto; }
  .footnote p,
  .graph-footnote p {
    color: #303030;
    font-family: "FFDINStdRegular";
    font-size: 0.75rem;
    margin: 0; }

@media all and (min-width: 768px) {
  .graph-footnote {
    width: 75%;
    margin: 0 auto; } }

.highlight {
  color: #eed931; }

p.hang {
  text-indent: -.5em; }

.top-indicator {
  cursor: pointer;
  color: #15345e;
  font-family: "FFDINStdRegular";
  font-size: 0.9375rem;
  position: relative;
  text-align: right;
  margin-right: 0.625rem; }
  @media all and (min-width: 768px) {
    .top-indicator {
      font-size: 1rem; } }
  .top-indicator:after {
    color: #15345e;
    content: "\e90e";
    font-family: icomoon !important;
    font-size: 0.375rem;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    right: -0.625rem; }

h1,
h2,
h3 {
  font-weight: bold;
  margin-top: 0;
  text-transform: uppercase;
  font-weight: normal; }

h4 {
  font-weight: normal; }

.logo-reduce-it {
